<template>
  <!-- 校验表管理 -->
  <el-container class="permission-assignment">
    <el-aside width='20%' class="permission-assignment-aside">
      <el-scrollbar :style="{'height':siteContentViewHeight - 10 + 'px' }">
        <org-tree ref="tree" :treeData="treeData"
          @node-click="orgTreeClick"></org-tree>
      </el-scrollbar>
    </el-aside>
    <el-main>
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="逻辑条件校验" name="first">
          <div class="permission-assignment-title">
            <span></span>
            <p>{{tabName}} 共{{dataList.length}}条记录</p>
          </div>
          <el-form :inline="true">
            <el-form-item>
              <el-button type="primary" size="small" @click="associationCheckRulesHandle">关联函数校验</el-button>
            </el-form-item>
          </el-form>
          <el-table
            :data="dataList"
            border
            v-loading="dataListLoading"
            :height="siteContentViewHeight - 140+'px'"
            style="width: 100%;">
            <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
            <el-table-column prop="ruleName" width="200" label="规则名称"></el-table-column>
            <el-table-column prop="ruleChecksingle" label="单记录校验"></el-table-column>
            <el-table-column prop="ruleCheckperson" width="300" label="人员校验"></el-table-column>
            <el-table-column prop="roleState" width="180" label="执行阶段">
              <template slot-scope="scope">
                保存前：<el-switch class="switchStyle" v-model="scope.row.checkBefor" active-value='1' inactive-value='0'
                active-text="启用" inactive-color="#e8e4f3" inactive-text="关闭" @change="(val)=>switchChange({checkBefor:val},scope.row.checkId)"></el-switch>
                <br />
                保存后：<el-switch class="switchStyle" v-model="scope.row.checkAfter" active-value='1' inactive-value='0'
                active-text="启用" inactive-color="#e8e4f3" inactive-text="关闭" @change="(val)=>switchChange({checkAfter:val},scope.row.checkId)"></el-switch>
              </template>
            </el-table-column>
            <el-table-column prop="checkStatus" width="100" label="启用状态">
              <template slot-scope="scope">
                <el-switch class="switchStyle" v-model="scope.row.checkStatus" active-value='1' inactive-value='0'
                active-text="启用" inactive-color="#e8e4f3" inactive-text="未启用" @change="(val)=>switchChange({checkStatus:val},scope.row.checkId)"></el-switch>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <!-- 弹窗，关联校验规则 -->
      <association-check-rules v-if="associationCheckRulesVisible" ref='associationCheckRules' @refreshDataList="getDataList"></association-check-rules>
    </el-main>
  </el-container>
</template>

<script>
  import orgTree from "@/components/baseComponents/orgTree.vue";
  import associationCheckRules from './association-check-rules.vue'
  export default{
    props:['siteContentViewHeight'],
    components:{
      orgTree,associationCheckRules
    },
    data(){
      return{
        treeData:[],
        activeName:'first',
        dataListAll:[],
        dataList:[],
        dataListLoading:false,
        associationCheckRulesVisible:false,
        tabId:'',
        tabName:''
      }
    },
    created () {
      this.getTreeData()
    },
    methods:{
      getTreeData(){
        this.$http({
          url: this.$http.adornUrl(this.API.getPersonDataTableTree),
          method: 'post',
          data: this.$http.adornData()
        }).then(({data}) => {
          this.treeData=[];
          if(data && data.code===0){
            data.data.forEach(item=>{
              this.treeData.push({
                id:item.tabCode,
                name:item.tabName
              })
            })
            this.orgTreeClick(this.treeData[0])
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      getDataList(){
        this.dataListLoading = true;
        this.$http({
          url: this.$http.adornUrl(this.API.getRuleList),
          method: 'get',
          params: this.$http.adornParams({
            tabId:this.tabId
          })
        }).then(({data}) => {
          this.dataList=[];
          if(data && data.code===0){
            this.dataListAll=data.data;
            data.data.forEach(row=> {
              if(row.isCheck==1){this.dataList.push(row)}
            });
          }else{
            this.$message.error(data.msg);
            this.dataListAll=[];
          }
          this.dataListLoading = false;
        });
      },
      orgTreeClick(data) {
        this.tabId=data.id;
        this.tabName=data.name;
        this.getDataList();
      },
      associationCheckRulesHandle(){
        this.associationCheckRulesVisible = true;
        this.$nextTick(() => {
          this.$refs.associationCheckRules.init(this.dataListAll,false,this.tabId)
        })
      },
      switchChange(e,id){
        var params={
          tabId:this.tabId,
          checkId:id
        }
        Object.assign(params,e);
        this.$http({
          url: this.$http.adornUrl(this.API.updateDataChecktable),
          method: 'post',
          data: this.$http.adornData(params)
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message.success('操作成功');
          }else{
            this.$message.error(data.msg);
          }
        });
      }
    }
  }
</script>
<style lang="scss" scoped>
  .permission-assignment{
    &-title{
      float: left;
      padding-left: 10px;
      color: #f0992b;
      span{
        display: inline-block;
        width:10px;
        height: 10px;
        background: rgba(#f0992b,0.6);
        box-shadow: 0px 0px 6px 0px #f0992b;border-radius: 50%;}
      p{display: inline-block; padding-left: 10px;}
      padding-right: 20px;
    }
    &-aside{
      padding: 10px 10px 0;
      border-right: 1px solid #e6e6e6;
      .el-scrollbar{padding: 8px 0;}
    }
    .el-form--inline .el-form-item{margin-bottom: 10px;margin-top: 0px;}
    .el-tabs{
      padding-left: 10px;
      position: relative;
      /deep/ .el-tabs__header::before{
        content:" ";
        width: 10px;
        height: 1px;
        position: absolute;
        left: -10px;
        top: 40px;
        background: #e4e7ed;
      }
      /deep/ .switchStyle{
        .el-switch__label {position: absolute;display: none;color: #fff;}
        .el-switch__label--left {z-index: 9;left: 20px;}
        .el-switch__label--right {z-index: 9;left: -2px;}
        .el-switch__label.is-active {display: block;}
        .el-switch__core,.el-switch__label {width: 62px!important;}
      }
    }
  }
</style>



